.jonnitto-prettyembed {
  position: relative;
  display: block;
  width: 100%;
  max-width: 100%;
}
.jonnitto-prettyembed img {
  display: block;
  width: 100%;
  height: auto;
  max-width: none;
}

.jonnitto-prettyembed--lightbox {
  cursor: pointer;
}

.jonnitto-prettyembed__lightbox {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0;
  font-size: 0;
  transition: opacity 0.2s, z-index 0s 0.2s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  overflow-y: auto;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  pointer-events: none;
  background: rgba(11, 11, 11, 0.8);
  padding: 15px;
  backdrop-filter: blur(5px);
}
.-jonnitto-prettyembed__lightbox .jonnitto-prettyembed__lightbox {
  z-index: 5500;
  opacity: 1;
  transition: opacity 0.2s;
  pointer-events: initial;
}

.jonnitto-prettyembed__inner {
  width: 100%;
  max-width: 900px;
  transform: scale(0);
  transition: transform 0.2s;
}
.-jonnitto-prettyembed__lightbox .jonnitto-prettyembed__inner {
  transform: scale(1);
}

.jonnitto-prettyembed__content {
  line-height: 0;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
}

.jonnitto-prettyembed__close {
  position: relative;
  display: block;
  z-index: 1;
  padding: 10px 16px;
  margin: calc(-1em - 10px) -18px -8px auto;
  font-size: 30px;
  font-family: Arial, Baskerville, monospace;
  font-style: normal;
  line-height: 1;
  cursor: pointer;
  user-select: none;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
  outline: none;
  box-shadow: none;
  text-decoration: none;
  color: #fff;
  opacity: 0.65;
  transition: opacity 0.2s linear;
}
.jonnitto-prettyembed__close:hover, .jonnitto-prettyembed__close:focus, .jonnitto-prettyembed__close:active {
  opacity: 1;
  outline: none;
}
.jonnitto-prettyembed__close::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.jonnitto-prettyembed-button {
  color: #fff;
  position: absolute;
  pointer-events: none;
  z-index: 1;
}

.jonnitto-prettyembed-button--play {
  display: block;
  top: 50%;
  left: 50%;
  width: 72px;
  height: 72px;
  transform: translate(-50%, -50%) scale(0.8);
  transition: transform 0.25s ease-in-out, opacity 0.25s linear;
  opacity: 0.9;
}
.jonnitto-prettyembed--play .jonnitto-prettyembed-button--play {
  opacity: 0;
}
.jonnitto-prettyembed:hover .jonnitto-prettyembed-button--play, .jonnitto-prettyembed:focus .jonnitto-prettyembed-button--play {
  transform: translate(-50%, -50%) scale(1);
}
.jonnitto-prettyembed:active .jonnitto-prettyembed-button--play {
  transform: translate(-50%, -50%) scale(0.9);
}

.jonnitto-prettyembed-button--pause {
  display: none;
  top: 18px;
  left: 18px;
  width: 36px;
  height: 36px;
  transition: opacity 0.25s linear;
  opacity: 0;
}
.jonnitto-prettyembed--video.jonnitto-prettyembed--play.jonnitto-prettyembed--slim .jonnitto-prettyembed-button--pause {
  display: block;
}
.jonnitto-prettyembed:hover .jonnitto-prettyembed-button--pause, .jonnitto-prettyembed:focus .jonnitto-prettyembed-button--pause {
  opacity: 0.9;
}

.jonnitto-prettyembed-button__background {
  fill: #000;
  fill-opacity: 0.4;
  stroke-width: 5;
}

.jonnitto-prettyembed video {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  max-width: 100%;
  outline: none;
  opacity: 0;
  margin: 0 auto;
}
.jonnitto-prettyembed video:not([controls]):not([autoplay]) {
  cursor: pointer;
}

.jonnitto-prettyembed--video img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: auto;
  transform: translate(-50%, -50%);
  display: block;
  transition: opacity 0.3s linear;
  pointer-events: none;
}
.jonnitto-prettyembed--video.jonnitto-prettyembed--autoplay {
  pointer-events: none;
}
.jonnitto-prettyembed--video.jonnitto-prettyembed--autoplay video, .jonnitto-prettyembed--video.jonnitto-prettyembed--init video {
  opacity: 1;
}
.jonnitto-prettyembed--video.jonnitto-prettyembed--autoplay img, .jonnitto-prettyembed--video.jonnitto-prettyembed--init img {
  opacity: 0;
}

.jonnitto-prettyembed__lightbox video {
  width: auto;
  max-height: calc(99vh - 53px);
}

.jonnitto-prettyembed--video.jonnitto-prettyembed--lightbox video {
  position: absolute;
  inset: 0;
  height: 100%;
}
.jonnitto-prettyembed--video.jonnitto-prettyembed--lightbox img {
  position: static;
  top: auto;
  left: auto;
  width: 100%;
  height: auto;
  transform: none;
}

.jonnitto-prettyembed--iframe iframe {
  border: 0 solid transparent;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.jonnitto-prettyembed--ratio[style*="padding-top:"] {
  height: 0;
  overflow: hidden;
}
.jonnitto-prettyembed--ratio[style*="padding-top:"] img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.jonnitto-prettyembed__gdpr {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1em;
  padding: 0.5em;
  color: #fff;
  z-index: 1;
  cursor: default;
  font-size: 0.8rem;
  backdrop-filter: blur(5px);
}
@media (min-width: 640px) {
  .jonnitto-prettyembed__gdpr {
    font-size: 1rem;
  }
}
.jonnitto-prettyembed__gdpr::after {
  content: "";
  position: absolute;
  inset: 0;
  background: #0b0b0b;
  opacity: 0.8;
  z-index: -1;
}
.jonnitto-prettyembed__gdpr * {
  margin: 0;
  padding: 0;
  font-size: 1em;
  text-align: center;
}
.jonnitto-prettyembed__gdpr p {
  font-size: 0.9em;
  max-width: 60ch;
  margin: 0 auto;
}
.jonnitto-prettyembed__gdpr div {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 1em;
}

.jonnitto-prettyembed__gdpr-button {
  display: block;
  padding: 0.5em 1em;
  text-decoration: none;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  cursor: pointer;
  border-radius: 0.25em;
}

.jonnitto-prettyembed__gdpr-button--accept {
  color: #fff;
  background-color: #16a34a;
  border: 1px solid #16a34a;
}
.jonnitto-prettyembed__gdpr-button--accept:hover, .jonnitto-prettyembed__gdpr-button--accept:focus {
  color: #fff;
  background-color: #15803d;
  border-color: #15803d;
}

.jonnitto-prettyembed__gdpr-button--external {
  color: #fff;
  background-color: transparent;
  border: 1px solid #fff;
}
.jonnitto-prettyembed__gdpr-button--external:hover, .jonnitto-prettyembed__gdpr-button--external:focus {
  color: #000;
  background-color: #fff;
}

